(function($) {
    $(window).resize(function() {
      $('.main .message_line').css({
        'width': $(window).width()});
    });

    var wh = window.innerHeight,
        sections = {
            main: $('.main')[0],
            services: $('.services')[0],
            bio: $('.bio')[0],
            documents: $('documents')[0]
        };

    var ctrl = new ScrollMagic.Controller({
        globalSceneOptions: {
            triggerHook: "onEnter"
        }
    });

    //main
    new ScrollMagic.Scene({
            triggerElement: sections.main
        })
    .setTween(new TimelineLite()
        .from($('.main .message_line'), 0.7, {
            width: 0,
            ease: Power1.easeInOut
        }, 0)
        .from($('.main .message_line .message'), 0.5, {
            opacity: 0,
            top: "10px",
            ease: Power1.easeInOut
        }, 0.6)
        .from($('.main .message_subline'), 0.5, {
            opacity: 0,
            ease: Power1.easeInOut
        }, 1.1))
    //.addIndicators()
    .addTo(ctrl);

    //services
    new ScrollMagic.Scene({
            triggerElement: sections.services,
            offset: $('.services h1').outerHeight()*2
        })
    .setTween(new TimelineLite()
        .from($('.services h1'), 0.5, {
            opacity: 0,
            ease: Power1.easeInOut
        }, 0))
    .reverse(false)
    //.addIndicators()
    .addTo(ctrl);

    $('.services .row > div').each(function() {
        new ScrollMagic.Scene({
            triggerElement: this,
            offset: $(this).outerHeight()
        })
        .setTween(new TimelineLite()
            .from($(this), 0.5, {
                opacity: 0,
                scale: 0.8,
                ease: Power1.easeInOut
            }, 0))
        .reverse(false)
        //.addIndicators()
        .addTo(ctrl);
    });

    //bio
    $('.bio .row h2').each(function() {
        new ScrollMagic.Scene({
            triggerElement: this,
            offset: $(this).outerHeight()
        })
        .setTween(new TimelineLite()
            .from($(this), 0.5, {
                opacity: 0,
                ease: Power1.easeInOut
            }, 0))
        .reverse(false)
        //.addIndicators()
        .addTo(ctrl);
    });

    $('.bio .row img').each(function() {
        new ScrollMagic.Scene({
            triggerElement: this,
            offset: $(this).outerHeight()/2
        })
        .setTween(new TimelineLite()
            .from($(this), 0.5, {
                opacity: 0,
                scale: 0.8,
                ease: Power1.easeInOut
            }, 0))
        .reverse(false)
        //.addIndicators()
        .addTo(ctrl);
    });

    $('.bio .row ul').each(function() {
        new ScrollMagic.Scene({
            triggerElement: this,
            offset: $(this).outerHeight()/2
        })
        .setTween(new TimelineLite()
            .from($(this), 0.5, {
                opacity: 0,
                ease: Power1.easeInOut
            }, 0))
        .reverse(false)
        //.addIndicators()
        .addTo(ctrl);
    });

    //documents
    new ScrollMagic.Scene({
            triggerElement: sections.documents,
            offset: 100
        })
        .setTween(new TimelineLite()
            .from($('.documents h1'), 1.5, {
                opacity: "0",
                ease: Power1.easeInOut
            }, 0))
        .reverse(false)
        //.addIndicators()
        .addTo(ctrl);

    /*$(".documents .doc_item").each(function() {
        new ScrollMagic.Scene({
                triggerElement: this,
                offset: $(this).innerHeight()
            })
            .setTween(new TimelineLite()
                .from($(this), 1.5, {
                    opacity: "0",
                    scale: "0.8",
                    ease: Power1.easeInOut
                }, 0))
            .reverse(false)
            //.addIndicators()
            .addTo(ctrl);
    })*/

})(jQuery);